// MAIN :: START

.main {
  overflow: hidden;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    mix-blend-mode: soft-light;
    background-image: url('../img/img-bg-main.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__bg {
    overflow: hidden;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100vw + 300px);
    margin-left: -150px;
    height: calc(100% - 102px);
    border-top-left-radius: 50% 0;
    border-top-right-radius: 50% 0;
    border-bottom-right-radius: 50% 100px;
    border-bottom-left-radius: 50% 100px;

    &-inner {
      width: 100%;
      height: 100%;
      @include background-gradient(#00449D  , #001632, 'horizontal');
    }
  }

  &__wrapper {
    z-index: 1;
    position: relative;
    padding-top: 146px;
    @include respond(max-small) {
      padding-top: 73px;
    }
  }

  &__subtitle,
  &__title {
    text-align: center;
  }

  &__subtitle {
    padding-bottom: 20px;
    color: rgba($c-white, 0.4);
    text-transform: uppercase;
    letter-spacing: 2.25px;
    font-weight: bold;
    @include fluid(font-size, 320px, 1440px, 16px, 18px);
  }

  &__title {
    padding-bottom: 35px;
    color: $c-white;
    line-height: 1.2;
    font-weight: 200;
    @include fluid(font-size, 320px, 1440px, 30px, 50px);
  }

  &__btn {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 70px;
  }

  &__video {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 31px);
    border-radius: 20px;
    background-color: #F7F7F7;

    &-wrapper {
      position: relative;
      width: 100%;
      max-width: 840px;
      //height: 504px;
      margin-left: auto;
      margin-right: auto;
      @include fluid(height, 320px, 1440px, 404px, 504px);

      &:before {
        z-index: 0;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url('../img/img-video-bg.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }

      video[poster]{
        height:100%;
        width:100%;
        object-fit: cover;
        object-position: center;
      }
    }

    &-btn {
      z-index: 2;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: calc(50% - 15px);
      left: 50%;
      transform: translate(-50%, -50%);
      width: 150px;
      height: 150px;
      border-radius: 50%;
      background-color: $c-white;

      &:hover {
        svg {
          transform: scale(0.9);
          @include transition(transform);
        }
      }

      svg {
        margin-left: 5px;
        fill: #003F91;
        transform-origin: center;
        @include transition(transform);
      }
    }

    &-progress {
      z-index: 2;
      cursor: pointer;
      overflow: hidden;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 71px;
      width: calc(100% - 100px);
      height: 10px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 7px;
      background-color: #E1E1E1;
      @include respond(max-small) {
        width: calc(100% - 30px);
        bottom: 30px;
      }

      span {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background-color: #CD084B;
        @include transition(width);
      }
    }
  }
}

// MAIN :: END