// LEADS :: START

.leads {
  overflow: hidden;
  border-bottom: 1px solid #D8D8D8;

  &__bg {
    z-index: 0;
    position: absolute;
    display: flex;
    align-items: flex-start;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include respond(max-small) {
      position: static;
      margin-bottom: 30px;
    }

    &-left,
    &-right {
      width: 50%;
      height: 100%;
      @include respond(max-small) {
        width: 100%;
      }
    }

    &-left {
      display: flex;
      align-items: flex-end;
      @include respond(max-medium) {
        align-items: center;
      }

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-right {
      @include respond(max-small) {
        display: none;
      }
    }
  }

  &__wrapper {
    z-index: 1;
    position: relative;
    padding-top: 99px;
    padding-bottom: 115px;
    @include respond(max-small) {
      padding-top: 50px;
      padding-bottom: 57px;
    }
  }

  &__row {
    display: flex;
    @include respond(max-small) {
      flex-wrap: wrap;
    }

    &-left,
    &-right {
      width: 50%;
      @include respond(max-small) {
        width: 100%;
      }
    }

    &-left {
      @include respond(max-small) {
        display: none;
      }
    }

    &-right {
      padding-left: 60px;
      @include respond(max-small) {
        padding-left: 0;
      }
    }
  }

  &__col {
    &-2 {
      z-index: 1;
      position: relative;
    }
  }

  &__text {
    text-align: left;

      &--pb {
      padding-bottom: 40px;
    }
  }

  &__btn {
    max-width: 232px;
  }
}

// LEADS :: END