// AI :: START

.ai {
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #D8D8D8;

  &__bg {
    z-index: 0;
    position: absolute;
    display: flex;
    align-items: flex-start;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include respond(max-small) {
      position: static;
      margin-bottom: 30px;
    }

    &-left,
    &-right {
      width: 50%;
      height: 100%;
      @include respond(max-small) {
        width: 100%;
      }
    }

    &-left {
      @include respond(max-small) {
        display: none;
      }
    }

    &-right {
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      @include respond(max-large) {
        align-items: center;
      }
      @include respond(max-medium) {
        align-items: center;
      }

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }

  &__wrapper {
    padding-top: 68px;
    padding-bottom: 118px;
    @include respond(max-small) {
      padding-top: 34px;
      padding-bottom: 30px;
    }
  }

  &__row {
    display: flex;
    @include respond(max-small) {
      flex-wrap: wrap;
    }

    &-left,
    &-right {
      width: 50%;
      @include respond(max-small) {
        width: 100%;
      }
    }

    &-left {
      padding-right: 60px;
      @include respond(max-small) {
        padding-right: 0;
      }
    }

    &-right {
      @include respond(max-small) {
        display: none;
      }

    }
  }

  &__col {
    &-2 {
      z-index: 1;
      position: relative;
    }
  }

  &__text {
    text-align: left;

    &--pb {
      padding-bottom: 40px;
    }
  }

  &__btn {
    max-width: 232px;
  }
}

// AI :: END