// PIPELINES :: START

.pipelines {
  position: relative;

  &__bg {
    position: absolute;
    top: 400px;
    left: 0;
    width: 100%;
    @include fluid(height, 320px, 1440px, 400px, 800px);
    @include respond(max-extra-small) {
      top: 480px;
    }

    img {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__wrapper {
    z-index: 1;
    position: relative;
    padding-top: 100px;
    padding-bottom: 118px;
    @include respond(max-small) {
      padding-top: 50px;
      padding-bottom: 59px;
    }
  }

  &__text {
    @include fluid(padding-bottom, 320px, 1440px, 400px, 700px);
  }

  &__tabs {
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    min-height: 30px;
    padding-left: 5px;
    padding-right: 5px;
    padding-bottom: 7px;
    margin-left: 25px;
    margin-right: 25px;
    color: #939393;
    font-weight: bold;
    text-align: center;
    @include transition(color);
    @include fluid(font-size, 320px, 1440px, 12px, 18px);
    @include fluid(margin-left margin-right, 320px, 1440px, 5px, 25px);
    @include respond(max-small) {
      width: 100%;
      max-width: calc(100% / 3);
    }

    &:first-of-type {
      margin-left: 0;
    }
    &:last-of-type {
      margin-right: 0;
    }

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: transparent;
      transform: translateX(-100%);
      @include transition(background-color, transform);
    }

    &.is-active {
      color: $c-black;
      @include transition(color);

      &:after {
        background-color: $c-black;
        transform: translateX(0);
        @include transition(background-color, transform);
      }
    }


    &-head {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &-body {
      width: 100%;
      max-width: 824px;
      margin-top: 30px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 13px;
      background-color: #F2F5FA;

      > div {
        display: none;
        min-height: 191px;
        padding: 35px 45px;
        align-items: center;
        justify-content: center;
        @include fluid(padding top padding-bottom, 320px, 1440px, 25px, 35px);
        @include fluid(padding-left padding-right, 320px, 1440px, 30px, 45px);

        &:first-of-type {
          display: flex;
        }

        p {
          line-height: 1.8;
          text-align: center;
        }
      }
    }
  }
}

// PIPELINES :: END